
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialising;
}

*, input, button{
  border: 0;
  outline: 0;
  font-family: 'Roboto', sans-serif; 
}

html, body, #root {
  height: 100%;
  background-color: var(--bg);
  color: var(--black);
  
  display: flex;
  align-items: center;
  justify-content: center;
}

:root {


  --main-color: #6FCF97;
  --black: #000000;
  --gray: #4B4B4B;
  --bg: #F4F4F4;


}