/* avatar */
.mepicture{
  flex: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}
.profilepic {
  
    position: relative;
   
}
.profilepic .avatar{
  width: 323px;
  height: 323px;
  border: 1px solid #e0e0e0;
  border-radius: 50%;
  background-image: url('./assets/avatar.jpg');
  background-size: cover;
  
  position: relative;
}

.avatar::after {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  background-color: var(--main-color);
  border-radius: 50%;
  position: absolute;
  right: 3px;
  top: 28px;
}


.avatar::before {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  background-color:  var(--main-color);
  border-radius: 50%;
  
  bottom: -20px;
  left: 10px;
  position: absolute;
}

.behindcircle {
  display: block;
  width: 175px;
  height: 175px;
  background-color: var(--main-color);
  border-radius: 50%;
  top: 90px;
  left: -70px;
  position: absolute;
}